import { MoveClassification } from "@/types/enums";
import { Box, Grid2 as Grid, Typography } from "@mui/material";
import MoveItem from "./moveItem";
interface Props {
  moves: {
    san: string;
    moveClassification?: MoveClassification;
  }[];
  moveNb: number;
}
export default function MovesLine({
  moves,
  moveNb
}: Props) {
  return <Grid container justifyContent="space-evenly" alignItems="center" wrap="nowrap" size={12} data-sentry-element="Grid" data-sentry-component="MovesLine" data-sentry-source-file="movesLine.tsx">
      <Typography width="2rem" fontSize="0.9rem" data-sentry-element="Typography" data-sentry-source-file="movesLine.tsx">
        {moveNb}.
      </Typography>

      <MoveItem {...moves[0]} moveIdx={(moveNb - 1) * 2 + 1} moveColor="w" data-sentry-element="MoveItem" data-sentry-source-file="movesLine.tsx" />

      {moves[1] ? <MoveItem {...moves[1]} moveIdx={(moveNb - 1) * 2 + 2} moveColor="b" /> : <Box width="5rem" />}
    </Grid>;
}