import { useSetAtom } from "jotai";
import { boardOrientationAtom } from "../states";
import { IconButton, Tooltip } from "@mui/material";
import { Icon } from "@iconify/react";
import { useEffect } from "react";
export default function FlipBoardButton() {
  const setBoardOrientation = useSetAtom(boardOrientationAtom);
  useEffect(() => {
    const onKeyDown = (e: KeyboardEvent) => {
      if (e.key === "f") {
        setBoardOrientation(prev => !prev);
      }
    };
    window.addEventListener("keydown", onKeyDown);
    return () => {
      window.removeEventListener("keydown", onKeyDown);
    };
  }, [setBoardOrientation]);
  return <Tooltip title="Flip board" data-sentry-element="Tooltip" data-sentry-component="FlipBoardButton" data-sentry-source-file="flipBoardButton.tsx">
      <IconButton onClick={() => setBoardOrientation(prev => !prev)} sx={{
      paddingX: 1.2,
      paddingY: 0.5
    }} data-sentry-element="IconButton" data-sentry-source-file="flipBoardButton.tsx">
        <Icon icon="eva:flip-fill" data-sentry-element="Icon" data-sentry-source-file="flipBoardButton.tsx" />
      </IconButton>
    </Tooltip>;
}