import { Grid2 as Grid, IconButton, Tooltip } from "@mui/material";
import { Icon } from "@iconify/react";
import { useAtomValue } from "jotai";
import { boardAtom, gameAtom } from "../states";
import { useChessActions } from "@/hooks/useChessActions";
import FlipBoardButton from "./flipBoardButton";
import NextMoveButton from "./nextMoveButton";
import GoToLastPositionButton from "./goToLastPositionButton";
import SaveButton from "./saveButton";
import { useEffect } from "react";
export default function PanelToolBar() {
  const board = useAtomValue(boardAtom);
  const {
    resetToStartingPosition: resetBoard,
    undoMove: undoBoardMove
  } = useChessActions(boardAtom);
  const boardHistory = board.history();
  const game = useAtomValue(gameAtom);
  useEffect(() => {
    const onKeyDown = (e: KeyboardEvent) => {
      if (boardHistory.length === 0) return;
      if (e.key === "ArrowLeft") {
        undoBoardMove();
      } else if (e.key === "ArrowDown") {
        resetBoard();
      }
    };
    window.addEventListener("keydown", onKeyDown);
    return () => {
      window.removeEventListener("keydown", onKeyDown);
    };
  }, [undoBoardMove, boardHistory, resetBoard, board]);
  return <Grid container justifyContent="center" alignItems="center" size={12} data-sentry-element="Grid" data-sentry-component="PanelToolBar" data-sentry-source-file="index.tsx">
      <FlipBoardButton data-sentry-element="FlipBoardButton" data-sentry-source-file="index.tsx" />

      <Tooltip title="Reset board" data-sentry-element="Tooltip" data-sentry-source-file="index.tsx">
        <Grid data-sentry-element="Grid" data-sentry-source-file="index.tsx">
          <IconButton onClick={() => resetBoard()} disabled={boardHistory.length === 0} sx={{
          paddingX: 2.5,
          paddingY: 0.5
        }} data-sentry-element="IconButton" data-sentry-source-file="index.tsx">
            <Icon icon="ri:skip-back-line" height={50} data-sentry-element="Icon" data-sentry-source-file="index.tsx" />
          </IconButton>
        </Grid>
      </Tooltip>

      <Tooltip title="Go to previous move" data-sentry-element="Tooltip" data-sentry-source-file="index.tsx">
        <Grid data-sentry-element="Grid" data-sentry-source-file="index.tsx">
          <IconButton onClick={() => undoBoardMove()} disabled={boardHistory.length === 0} sx={{
          paddingX: 2.5,
          paddingY: 0.5
        }} data-sentry-element="IconButton" data-sentry-source-file="index.tsx">
            <Icon icon="ri:arrow-left-s-line" height={50} data-sentry-element="Icon" data-sentry-source-file="index.tsx" />
          </IconButton>
        </Grid>
      </Tooltip>

      <NextMoveButton data-sentry-element="NextMoveButton" data-sentry-source-file="index.tsx" />

      <GoToLastPositionButton data-sentry-element="GoToLastPositionButton" data-sentry-source-file="index.tsx" />

      <Tooltip title="Copy pgn" data-sentry-element="Tooltip" data-sentry-source-file="index.tsx">
        <Grid data-sentry-element="Grid" data-sentry-source-file="index.tsx">
          <IconButton disabled={game.history().length === 0} onClick={() => {
          navigator.clipboard?.writeText?.(game.pgn());
        }} sx={{
          paddingX: 2.5,
          paddingY: 0.5
        }} data-sentry-element="IconButton" data-sentry-source-file="index.tsx">
            <Icon icon="ri:clipboard-line" data-sentry-element="Icon" data-sentry-source-file="index.tsx" />
          </IconButton>
        </Grid>
      </Tooltip>

      <SaveButton data-sentry-element="SaveButton" data-sentry-source-file="index.tsx" />
    </Grid>;
}